import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
import store from '@/store';

const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err);
};
const originalReplace = VueRouter.prototype.replace;
VueRouter.prototype.replace = function push(location) {
	return originalReplace.call(this, location).catch(err => err);
};

const router = new VueRouter({
	mode: process.env.NODE_ENV !== 'production' ? 'hash' : 'history',
	routes: [
		{
			path: '/',
			component: () => import('@/views/home/Home.vue'),
			children: [
				{ path: '/', name: 'home', component: () => import('@/views/home/children/GoodsList.vue') },
				{ path: '/typelist/:type', name: 'typeList', component: () => import('@/views/home/children/TypeList.vue') },
			],
		},
		{ path: '/login', name: 'login', component: () => import('@/views/Login.vue') },
		{ path: '/regist', name: 'regist', component: () => import('@/views/Regist.vue') },
		{ path: '/search', name: 'search', component: () => import('@/views/SearchView.vue') },
		{
			path: '/cart',
			component: () => import('@/views/CartView.vue'),
			name: 'cart',
			meta: {
				requireAuth: true, // 添加该字段，表示进入这个路由是需要登录的
			},
		},

		{ path: '/goodsdetail/:goodsId', name: 'goodsdetail', component: () => import('@/views/GoodsDetail.vue') },
		{ path: '/supplier/:supplier', name: 'supplier', component: () => import('@/views/SupplierView.vue') },
	],
	scrollBehavior(to, from, savedPosition) {
		if (savedPosition) {
			return savedPosition;
		} else {
			return { x: 0, y: 0 };
		}
	},
});

router.beforeEach((to, from, next) => {
	// 导航到新页面时是否需要登录权限
	if (to.meta.requireAuth) {
		// 判断该路由是否需要登录权限
		if (store.getters.token) {
			// token存在，已经登录，正常导航页面
			next();
		} else {
			// token不存在，去登录页面
			next({
				path: '/login',
				query: { redirect: to.fullPath }, // 将跳转的路由path作为参数，登录成功后跳转到该路由
			});
		}
	} else {
		next();
	}
});

export default router;
