import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);

import user from './modules/user';
import cart from './modules/cart';
import { getStore, setStore, removeStore, getStoreJSON } from '@/config/storage';

export default new Vuex.Store({
	strict: process.env.NODE_ENV !== 'production',
	state: {
		status: '',
    typeOneIndex: getStore('typeOneIndex') || -1,
    typeOneList:[],
	},
	getters: {
		status: state => state.status,
		typeOneIndex: state => state.typeOneIndex,
	},
	mutations: {
		SET_STATUS(state, status) {
			state.status = status;
		},
		SET_TYPE_ONE_LIST(state, val) {
			state.typeOneList = val;
		},
		SET_TYPE_ONE_INDEX(state, val) {
			state.typeOneIndex = val;
			setStore('typeOneIndex', val);
		},
	},
	actions: {},
	modules: {
		user,
		cart,
	},
});
