import { getCartListApi, addCartApi, updateCartApi, delCartApi } from '@/api/cart';
import { Message } from 'element-ui';
import { getStore, setStore, removeStore, getStoreJSON } from '@/config/storage';

export default {
	state: () => ({
		cartList: getStoreJSON('cartList') || [],
		count: 0,
	}),
	getters: {
		cartList: state => state.cartList,
		count: state => {
			return state.cartList.reduce((prev, current) => {
				return prev + current.count;
			}, 0);
		},
	},
	mutations: {
		SET_CART_LIST(state, list = []) {
			state.cartList = list;
			state.cartList.length ? setStore('cartList', state.cartList) : removeStore('cartList');
		},
		DET_CART_LIST(state, goods) {
			let index = state.cartList.indexOf(goods);
			state.cartList.splice(index, 1);
			setStore('cartList', state.cartList);
		},
		CHANGE_CART_LIST(state, { goods, count }) {
			let index = state.cartList.indexOf(goods);
			state.cartList[index].count = count;
			setStore('cartList', state.cartList);
		},
	},
	actions: {
		async getCartList({ commit }) {
			let res = await getCartListApi();
			commit('SET_CART_LIST', res);
		},
		async addCart({ dispatch }, { goodId, count }) {
			let res = await addCartApi({ goodId, count });
			if (res.code == 1) {
				Message.success('添加成功');
				await dispatch('getCartList');
			}
			if (res.code == 2) Message.warning('添加失败');
		},
		async updateCart({ commit }, { goods, count }) {
			let res = await updateCartApi({ goodId: goods.Id, count });
			if (res.code == 1) {
				Message.success('更新成功');
				commit('CHANGE_CART_LIST', { goods, count });
			}
			if (res.code == 2) Message.warning('更新失败');
		},
		async delCart({ commit }, goods) {
			let res = await delCartApi({ goodId: goods.Id });
			if (res.code == 1) {
				Message.success('删除成功');
				commit('DET_CART_LIST', goods);
			}
			if (res.code == 2) Message.warning('删除失败');
		},
	},
};
