<template>
	<header class="header-nav">
		<div class="w">
			<div class="header-left">
				<!-- 商标 -->
				<div class="logo">
					<router-link to="/"><img class="logo-img" src="@/assets/imgs/logo.png" /></router-link>
				</div>
				<!-- 导航选项 -->
				<nav class="nav-list">
					<div class="nav-item" :class="{ active: navIndex == 0 }">
						<router-link to="/">首页</router-link>
					</div>
					<div class="nav-item" :class="{ active: navIndex == 1 }">
						<el-badge v-if="token" :value="$store.getters.count" :max="99">
							<router-link to="/cart">购物车</router-link>
						</el-badge>
						<router-link v-else to="/cart">购物车</router-link>
					</div>
					<div class="nav-item" :class="{ active: navIndex == 2 }">
						<a v-if="token" href="javascript:;">{{ user.userName }}</a>
						<router-link v-else to="/login">登录</router-link>
					</div>
					<div class="nav-item" :class="{ active: navIndex == 3 }">
						<a v-if="token" @click="$store.dispatch('logout')" href="javascript:;">退出</a>
						<router-link v-else to="/regist">注册</router-link>
					</div>
				</nav>
			</div>

			<!-- 回车键和点击按钮都可以搜索 -->
			<div class="search header-left" >
				<el-input v-model.trim="keyword" @keyup.native.enter="search" placeholder="请输入想要的商品" clearable>
					<el-button @click="search" slot="append" icon="el-icon-search">搜索</el-button>
				</el-input>
			</div>
		</div>
	</header>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
	name: 'header-nav',
	data() {
		return {
			keyword: '',
			navIndex: 0,
		};
	},
	computed: {
		...mapGetters(['token', 'user']),
	},
	methods: {
		search() {
			if (!this.keyword) {
				this.$message({
					message: '搜索内容不能为空',
					type: 'warning',
				});
			} else {
				this.$router.push('/search?keyword=' + this.keyword);
			}
		},
	},
	watch: {
		$route(to) {
			// console.log('to=',to);
			switch (to.name) {
				case 'home':
					this.navIndex = 0;
					this.$store.commit('SET_TYPE_ONE_INDEX', -1);
					break;
				case 'cart':
					this.navIndex = 1;
					break;
				case 'login':
					this.navIndex = 2;
					break;
				case 'regist':
					this.navIndex = 3;
					break;
			}
			if (to.path.indexOf('/goodsdetail') > -1) {
				this.navIndex = 0;
			}
		},
	},
};
</script>

<style scoped lang="scss">
.header-nav {
	width: 100%;
	height: 60px;
	background-color: #fff;
	box-shadow: 0 0 3px #333;
	font-weight: 700;
	z-index: 99;
  position: fixed;
  top: 0;
  left: 0;

	.w {
		display: flex;
		justify-content: space-between;
    align-items: center;
	}

	.header-left {
		display: flex;
		.logo {
			height: 60px;
			padding: 5px 0;
			margin-right: 30px;
			.logo-img {
				height: 50px;
			}
		}
	}

	.nav-list {
		height: 60px;
		padding: 10px 0;
		display: flex;
		align-items: center;

		.nav-item {
			height: 100%;
			margin-right: 10px;
			border-bottom: 2px solid transparent;
			a {
				display: inline-block;
				padding: 0 10px;
				height: 100%;
				line-height: 40px;
				text-align: center;
			}
			&:hover {
				color: #800080;
				border-bottom: 2px solid #800080;
			}
		}
		.active {
			color: #800080;
			border-bottom: 2px solid #800080;
		}
	}
}
</style>
