import { getApi } from '@/config/http';


/**
 * 获取购物车列表
 * @param {Object} data 请求参数
 */
export const getCartListApi = data => getApi('/shopList',data);
/**
 * 添加购物车
 * @param {Object} data 请求参数
 */
export const addCartApi = data => getApi('/add',data);
/**
 * 更新购物车
 * @param {Object} data 请求参数
 */
export const updateCartApi = data => getApi('/update',data);
/**
 * 删除购物车
 * @param {Object} data 请求参数
 */
export const delCartApi = data => getApi('/del',data);
