import _ from 'lodash';

const plugin = {
	install(Vue) {
		Vue.prototype.$_ = _;

		Vue.filter('number', function (value) {
			if (value > 10000) {
				return (value / 10000).toFixed(2) + '万';
			} else {
				return value;
			}
		});
		Vue.filter('price', function (value) {
			if (!isNaN(value)) {
				return '￥ ' + Number(value).toFixed(2);
			} else {
				return value;
			}
		});
	},
};

export default plugin;
