import axios from 'axios';
import { Message } from 'element-ui';
import store from '@/store';
import router from '@/router';

// 创建axios实例
const service = axios.create({
	baseURL: process.env.VUE_APP_URL,
	timeout: 3 * 1000,
});

const whiteList = ['/add', '/del', '/update', '/login', '/regist'];

// 添加请求拦截器
service.interceptors.request.use(
	config => {
		// 如果存在token，统一在http请求的header都加上token，这样后台根据token判断你的登录情况
		// 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行判断
		if (store.getters.token) {
			config.headers.authorization = 'Bearer ' + store.getters.token;
		}
		if (!whiteList.includes(config.url)) {
			store.commit('SET_STATUS', 'loading');
		}
		return config;
	},
	error => {
		// 对请求错误做些什么
		return Promise.reject(error);
	}
);

// 添加响应拦截器
service.interceptors.response.use(
	async response => {
		// 对响应数据做点什么
		let data = response.data;
		store.commit('SET_STATUS', 'success');

		// 登录失效的统一处理: 未登录 或者 登录之后，本地还保存着token，但是token失效了
		if (data.code == 0) {
			Message.warning('验证失败，请重新先登录');
			store.dispatch('clearData');
			router.replace({
				path: '/login',
				query: { redirect: router.currentRoute.fullPath },
			});
		}
		console.log('响应结果：', data);
		return data;
	},
	error => {
		// 对响应错误做点什么
		store.commit('SET_STATUS', 'fail');
		Message.error(error.message);
		return Promise.reject(error);
	}
);

/**
 * 请求的封装
 */
const callApi = (url, method = 'get', data = {}) => {
	return service({
		url,
		method,
		params: method === 'get' ? data : {},
		data: method === 'post' ? data : {},
	});
};

/**
 * GET请求
 */
export const getApi = (url, data) => callApi(url, 'get', data);
/**
 * POST请求
 */
export const postApi = (url, data) => callApi(url, 'post', data);
