import { getStore, setStore, removeStore, getStoreJSON } from '@/config/storage';
import { Message } from 'element-ui';
import { loginApi } from '@/api/user';
import router from '@/router';

export default {
	state: () => ({
		token: getStore('token') || '',
		user: getStoreJSON('user') || null,
	}),
	getters: {
		token: state => state.token,
		user: state => state.user,
	},
	mutations: {
		SET_TOKEN(state, token) {
			state.token = token;
			//设置token的时候，同时把token保存到本地
			state.token ? setStore('token', token) : removeStore('token');
		},
		SET_USER(state, user) {
			state.user = user;
			//设置user的时候，同时把user保存到本地
			state.user ? setStore('user', user) : removeStore('user');
		},
	},
	actions: {
		async login({ commit, dispatch }, data) {
			let res = await loginApi(data);
			if (res.code == 0) {
				Message.warning('用户名或密码错误');
			} else {
				Message.success('登录成功');
				commit('SET_TOKEN', res.token);
				commit('SET_USER', { userName: res.userName, userId: res.userId });
				// 如果存在redirect参数，登录后重定向到指定页面，否则回到首页
				let path = router.currentRoute.query.redirect;
				if (path) {
					router.replace(path);
				} else {
					router.replace('/');
				}
				// 登录成功获取购物车列表
				dispatch('getCartList');
			}
		},
		// 把状态中的token和user置空，并把保存在本地的token和user删除
		clearData({ commit }) {
			commit('SET_TOKEN', '');
			commit('SET_USER', null);
			commit('SET_CART_LIST', []);
		},
		// 退出登录
		logout({ dispatch }) {
			dispatch('clearData');
			router.replace('/');
		},
	},
};
